import React, {useEffect, useRef, useState} from "react";
import "bootstrap/dist/css/bootstrap.min.css"
import {instance, url} from "../../axios.js";
import {useForm} from "react-hook-form"
import TextField from "@mui/material/TextField";
import {useNavigate} from 'react-router-dom';
import Select from "@mui/material/Select";
import {MenuItem} from "@mui/material";
import {HallForm} from "../Halls/HallForm";
import {Hall} from "../Halls/Hall";

String.prototype.trimPart = function (start, length) {
    return this.length > length ? this.substring(start, length) : this;
}

export const PerformanceChoose = (props) => {
        const navigation = useNavigate()
        const [errorMsg, setErrorMsg] = useState("")
        const [timeFull, setTimeFull] = useState("2000-01-01T00:00:00.000+00:00")
        const [hall, setHall] = useState("")
        const [show, setShow] = useState("")
        const [performanceAvatarUrl, setPerformanceAvatarUrl] = useState("/uploads/show.jpg")
        const [time, setDuration] = useState(timeFull.trimPart(11, 16))
        const [date, setDate] = useState(timeFull.trimPart(0, 10))
        const [dateFull, setDateFull] = useState("2000-01-01T00:00:00.000+00:00")
        const [details, setDetails] = useState("")
        const [creation, setCreation] = useState(false)
        const update = props.update
        const performance = props.performance
        const inputFileRef = useRef(null);
        const [halls, setHalls] = useState([])
        const [shows, setShows] = useState([])
        const myRef = useRef()

        const {handleSubmit, formState: {errors}} = useForm({})

        String.prototype.replaceAt = function (index, replacement) {
            return this.substring(0, index) + replacement + this.substring(index + replacement.length);
        }


        const performanceSubmit = () => {
            try {
                instance.post('/show/create', {
                    performanceTime: timeFull,
                    details: details,
                    hall: hall,
                    show: show,
                    performanceAvatarUrl: performanceAvatarUrl,
                }).then(res => {
                    setCreation(true)
                }).catch(error => {
                    if (error.response.status === 400) {
                        const msg = error.response.data[0];
                        setErrorMsg(msg.msg);
                    } else {
                        setErrorMsg(error.response.data.message);
                    }
                })
            } catch (error) {
                if (error.response) {
                    console.log(error.response.data.message);
                }
            }
        }

        const performanceUpdate = () => {
            try {
                instance.patch(`/performance/update/${performance._id}`, {
                    performanceTime: timeFull,
                    details: details,
                    hall: hall,
                    show: show,
                    performanceAvatarUrl: performanceAvatarUrl,
                }).then(res => {
                    props.cancelling("")
                }).catch(error => {
                    if (error.response.status === 400) {
                        const msg = error.response.data[0];
                        setErrorMsg(msg.msg);
                    } else {
                        setErrorMsg(error.response.data.message);
                    }
                })
            } catch (error) {
                if (error.response) {
                    console.log(error.response.data.message);
                }
            }
        }
        const getHalls = () => {
            try {
                instance.get(`/hall/all`).then(res => {
                    setHalls(res.data)
                })
                    .catch(error => {
                        console.log(error.response.data.message);
                    })
            } catch
                (error) {
                if (error.response) {
                    console.log(error.response.data.message);
                }
            }
        }

        const getShows = () => {
            try {
                instance.get(`/show/all`).then(res => {
                    setShows(res.data)
                })
                    .catch(error => {
                        console.log(error.response.data.message);
                    })
            } catch
                (error) {
                if (error.response) {
                    console.log(error.response.data.message);
                }
            }

            if (creation) {
                navigation('/performances', {
                    state: {
                        msg: `You have created new performance - ${show.name} on ${timeFull.trimLen(16)}`,
                    }
                });
            }
        }

        useEffect(() => {
            if (performance) {
                setTimeFull(performance.performanceTime)
                setShow(performance.show)
                setHall(performance.hall)
                setDetails(performance.details)
            }
        }, [show])


        useEffect(() => {
            setTimeFull(timeFull.replaceAt(0, date))
            setDateFull(dateFull.replaceAt(0, date))
        }, [date])

        useEffect(() => {
            setTimeFull(timeFull.replaceAt(11, time))
        }, [time])

        const handleChangeFile = async (event) => {
            try {
                const formData = new FormData();
                const file = event.target.files[0];
                formData.append("image", file);
                const {data} = await instance.post("/upload", formData)
                setPerformanceAvatarUrl(data.url)
            } catch
                (error) {
                alert("Couldn`t upload the image")
                if (error.response) {
                    console.log(error.response.data.message);
                }
            }
        }

        return (
            <div>
                <div className="Auth-form-container">
                    <form className="form">
                        <div className="Auth-form-content">
                            <h3 className="Auth-form-title">{update ? "Update the performance" : "Create a new performance"}</h3>
                            <div className="form-group mt-sm-0">
                                <label>Choose date</label>
                                <TextField
                                    required
                                    id={""}
                                    type="date"
                                    value={date}
                                    onChange={e => setDate(e.target.value)}
                                    className="form-control mt-sm-0"
                                    placeholder="Enter date"
                                    error={Boolean(errors.date?.message)}
                                    helperText={errors.date?.message}
                                />
                            </div>
                            <div className="d-grid gap-2 mt-3">
                                <button type="button" onClick={() => {
                                    getHalls()
                                }} className="btn btn-primary">
                                    Submit
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
                <div className={"d-flex justify-content-center"}>
                    <div className={"gallery"}>
                        {
                            halls ?
                                halls.map((h) =>
                                    <>
                                        <button onDoubleClick={() => {
                                            alert("Srfd")
                                        }}/>
                                        <Hall hall={h} key={h._id}/></>) : ""
                        }
                    </div>
                </div>
            </div>)
    }
;